figure {
  width: 100%;
  background: #f2f2f2;
  .inner {
    width: 1180px;
    padding-top: 120px;
    margin: 0 auto;
    .text-box {
      display: flex;
      justify-content: space-between;
      .text-box-left {
        h2 {
          font-size: 60px;
          display: flex;
          p:nth-of-type(2) {
            padding-left: 24px;
          }
        }
        .mini-text-box {
          display: flex;
          position: relative;
          margin: 30px 0;
          // background: red;
          p {
            font-size: 18px;
            &:nth-of-type(1) {
              display: flex;
              align-items: center;
              &::after {
                flex-grow: 1;
                content: '';
                height: 1px;
                background: #000;
                margin: 0 16px;
                width: 200px;
              }
            }
            &:nth-of-type(2) {
              &::after {
                position: absolute;
                top: 50%; right: 0;
                transform: translateY(-50%);
                content: '→';
                padding: 6px 9px;
                border: 1px solid #000;
                border-radius: 50%;
                font-size: 18px;
              }
            }
          }
        }
      }
      .text-box-right {
        display: flex;
        align-items: flex-end;
        position: relative;
        width: 500px;
        padding-bottom: 30px;
        p {
          position: absolute;
          font-weight: bold;

          &:nth-of-type(1) {
            bottom: 0;; right: 0px;
            font-size: 180px;
            color: #fff;
          }
          &:nth-of-type(2) {
            font-size: 34px;
            right: 0;
          }

        }
      }
    }
    .img-box {
      width: 100%; height: 600px;
      img {
        width: 100%; height: 100%;
        object-fit: cover;
      }
    }
  }

}