footer {
  width: 100%; height: 400px;
  background: #222;
  color: #ddd;
  .inner {
    width: 1180px; height: 100%;
    // padding: 50px 0;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-content: center;
    flex-wrap: wrap;
    .footer-box {
      width: 500px;
      margin: 20px 0;
      letter-spacing: 1px;
      &:nth-of-type(1) {
        h2 {
          font-size: 46px;
        }
      }
      &:nth-of-type(2) {
        p {
          font-size: 20px;
        }
      }
      &:nth-of-type(3) {
        p {
          margin-bottom: 20px;
        }
        input {
          width: 70%; height: 30px;
          padding-left: 10px;
          font-size: 12px;
          letter-spacing: 1px;
        }
      }
      &:nth-of-type(4) {
        display: flex;
        justify-content: space-between;
        ul {
          p {
            font-weight: bold;
            font-size: 20px;
            margin-bottom: 12px;
            
          }
          li {
            font-weight: normal;
            font-size: 14px;
            margin: 3px 0;
            a {
              color: #ddd;
            }
          }
        }
      }
    }
    
  }
}